import React from 'react';
import PropTypes from 'prop-types';
import FocusTrap from 'react-focus-trap';
import Headline from '../Headline';
import disableScrolling from '../../HOC/disableScrolling';
import './DialogWindow.scss';

const DialogWindow = ({ text, children, picUrl, picAlt, className }) => (
  <FocusTrap>
    <div className="dialog-window__wrapper">
      <div className="dialog-window">
        <img src={picUrl} alt={picAlt} />
        <Headline className="dialow-window__headline">{text}</Headline>
        <div className={className}>
          {children}
        </div>
      </div>
    </div>
  </FocusTrap>
);

DialogWindow.propTypes = {
  text: PropTypes.string.isRequired,
  picUrl: PropTypes.string.isRequired,
  picAlt: PropTypes.string.isRequired,
  className: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.element),
    PropTypes.element,
  ]).isRequired,
};

DialogWindow.defaultProps = {
  className: '',
};

export default disableScrolling(DialogWindow);
